  --animation-timing-function: cubic-bezier(0, .76, .18, 1);
}

*, body {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Roboto, sans-serif;
}

h1, h2, h3, h4, h5, h6 {
  margin: 0;
  padding: 0;
  font-family: Raleway, sans-serif;
}

p {
  margin: 0;
  padding: 0;
  font-family: Roboto, sans-serif;
}

a {
  text-decoration: none;
}

ul {
  padding: 0;
  list-style: none;
}

button {
  cursor: pointer;
  border: none;
  outline: none;
}

figure {
  margin: 0;
}

img {
  display: block;
}

.d-none {
  display: none;
}

.d-block {
  display: block;
}

body {
  height: 100vh;
  display: grid;
}

.site-header {
  width: auto;
  height: 230px;
  flex-direction: column;
  padding: 20px;
  display: flex;
}

.site-header.main {
  background-image: url("header-bg.35df3794.jpg");
  background-size: cover;
}

.site-header.mylibrary {
  background-image: url("mylibrary-header-bg.89899aaa.jpg");
  background-size: cover;
}

.site-header .logo {
  color: var(--white);
  margin-left: 14px;
  font-size: 30px;
  display: none;
}

nav {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.logo-container {
  justify-content: center;
  align-items: center;
  display: flex;
}

ul {
  justify-content: right;
  gap: 41px;
  display: flex;
}

nav ul li a {
  color: var(--white);
  font-size: 12px;
}

.active {
  text-decoration: underline;
  -webkit-text-decoration-color: var(--active);
  text-decoration-color: var(--active);
  text-decoration-thickness: 3px;
}

.form-container {
  flex-direction: column;
  align-items: center;
