.search-form {
  width: auto;
  flex-direction: row;
  align-items: center;
  margin-top: 42px;
  display: flex;
}

#search-input {
  border-width: 0 0 2px;
  border-color: var(--footer-text);
  z-index: 1;
  width: 100%;
  color: var(--white);
  background-color: rgba(0, 0, 0, 0);
  outline: 0;
  padding: 5px 0;
  position: absolute;
  top: 8%;
  left: 0%;
}

::-ms-input-placeholder {
  color: var(--white);
  font-size: 14px;
}

::placeholder {
  color: var(--white);
  font-size: 14px;
}

.search-btn {
  border-color: var(--white);
  z-index: 5;
  background-color: rgba(0, 0, 0, 0);
  position: absolute;
  top: 8%;
  right: 0%;
}

.alert-text {
  color: var(--alert);
  margin-top: 60px;
  margin-bottom: 36px;
  font-size: 14px;
}

.btn-container {
  justify-content: center;
  gap: 20px;
  margin: 48px 0 65px;
  display: flex;
}

.btn {
  width: 130px;
  height: 44px;
  color: var(--white);
  border: 1px solid var(--white);
  cursor: pointer;
  background: none;
  border-radius: 5%;
}

.btn:active {
  background-color: var(--active);
  border: 1px solid var(--active);
}

main {
  align-self: center;
  justify-self: center;
  padding: 20px;
}

#pagination {
  width: 100%;
  z-index: 9999;
  text-align: center;
  margin: auto;
  bottom: 0;
  left: 0;
}

.site-footer {
  background-color: var(--footer-bg);
  text-align: center;
  align-self: end;
  padding: 1.5em 0;
}

.site-footer span {
  color: var(--footer-text);
  font-size: 14px;
}

.site-footer a {
  color: var(--footer-text);
  font-size: 14px;
  text-decoration: underline;
}

span.heart {
