#modal {
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, .5);
  justify-content: center;
  align-items: center;
  display: none;
  position: fixed;
  top: 0;
  left: 0;
}

.modal-content {
  background-color: #fff;
  padding: 20px;
}

.modal-btn {
  width: 113px;
  height: 44px;
  color: var(--black);
  border: 1px solid var(--black);
  cursor: pointer;
  background: none;
  border-radius: 5%;
}

.modal-btn:active {
  background-color: var(--active);
  border: 1px solid var(--active);
  color: var(--white);
}

.close-button {
  width: 30px;
  height: 30px;
  background-color: rgba(0, 0, 0, 0);
  border: 0;
  justify-content: center;
  align-items: center;
  display: flex;
  position: absolute;
  top: 2.5%;
  left: 90%;
}

.close-button:hover > .close-icon, .close-button:active > .close-icon {
  stroke: var(--active);
}

