.loader {
  width: 48px;
  height: 48px;
  box-sizing: border-box;
  border: 5px solid #fff;
  border-bottom-color: #ff6b08;
  border-radius: 50%;
  animation: rotation 1s linear infinite;
  display: inline-block;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

main {
  width: 100%;
  height: auto;
  box-sizing: border-box;
  margin: 0 auto;
  display: block;
}

#movie-container {
  flex-wrap: wrap;
  justify-content: space-between;
  margin-bottom: 30px;
  display: flex;
}

.movie-card {
  box-sizing: border-box;
  object-fit: cover;
  cursor: pointer;
  width: calc(33.33% - 20px);
  border: none;
  margin-bottom: 30px;
  overflow: hidden;
}

.movie-card img {
  width: 100%;
